import React from 'react'
import Link from 'gatsby-link'
import styled, { keyframes } from 'styled-components'
import Empty from '../assets/cursor/empty.svg'
import Filled from '../assets/cursor/filled.svg'

const start = keyframes`
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
`;

const Start = styled.div`
  opacity: 0;
  animation: ${start} 3s 0.2s cubic-bezier(0.2, 0.8, 0.2, 1);
  animation-fill-mode: forwards;
`;

const rainbow = keyframes`
  0% {

        color: #ffe0e0;
      }

      10% {

        color: #ffe1ac;
      }

      20% {

        color: #fffaa8;
      }

      30% {

        color: #bffaa8;
      }

      40% {

        color: #bff3e0;
      }

      50% {

        color: #9fe0f8;
      }

      60% {

        color: #9fbaed;
      }

      70% {

        color: #ad99d8;
      }
      80% {

        color: #9f7af8;
      }

      90% {

        color: #c48bf6;
      }

      100% {

        color: #e6b1f6;
      }
`;

const StyledText = styled.span`
  font-family: 'Snell Roundhand', 'Snell Roundhand Script', serif;
  font-weight: bold;
  transition: 0.8s cubic-bezier(0.2, 0.8, 0.2, 1);

  :hover {
    animation: ${rainbow} 20s infinite alternate;
    animation-fill-mode: forwards;
  }
`;

const Content = styled.div`
  z-index: 10;
  height: 100%
  background-size: cover;
  background-position: center;
  cursor: url(../assets/cursor/empty.svg), auto;
  position: float;
`;

const ContentGroup = styled.div`
  margin: 0 auto;
  max-width: 1180px;
  font-size: 100px;
  padding: 50px 0px;
  transition: 0.8s cubic-bezier(0.2, 0.8, 0.2, 1);

  .Hover {
    background-color: #ffffff;
    color: #000000;
  }

  @media (max-width: 1382px) {
    max-width: 80%
  }

  @media (max-width: 1024px) {
    font-size: 80px;
  }

  @media (max-width: 768px) {
    max-width: 90%
    font-size: 60px;
    padding: 15px 0px;
  }
`;

class NotFoundPage extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      hover: false
    }
  }

  mouseOver = () => {
    this.setState({hover: true})
    console.log(this.state.hover)
  }

  mouseOut = () => {
    this.setState({hover: false})
    console.log(this.state.hover)
  }

  render() {
    return (
      <div>
        <Start>
          <Content>
            <ContentGroup>
              <p>
                WOW, THIS PAGE&nbsp;

                <StyledText onMouseOver={this.mouseOver} onMouseOut={this.mouseOut}>
                Doesn't Exist
                </StyledText>.
              </p>
            </ContentGroup>
          </Content>
        </Start>
      </div>
    )
  }
}

export default NotFoundPage



